"use client";
import { useEffect, useState } from "react";
import { jwtDecode } from "jwt-decode";
import PersonalDetails from "../components/";
import ProfessionalDetails from "../components/ProfessionalDetails";
import EducationDetails from "../components/EducationDetails";

export default function Dashboard() {
  const [user, setUser] = useState(null);
  const [activeTab, setActiveTab] = useState("personal");

  useEffect(() => {
    const token = localStorage.getItem("token");
    if (!token) return;

    const decoded = jwtDecode(token);
    fetchUser(decoded.userId);
  }, []);

  const fetchUser = async (id) => {
    try {
      const res = await fetch(`/api/user/${id}`);
      const data = await res.json();
      if (res.ok) {
        setUser(data.user);
      }
    } catch (err) {
      console.error("Error fetching user", err);
    }
  };

  const getGreeting = () => {
    const hour = new Date().getHours();
    if (hour < 12) return "Good Morning";
    else if (hour < 18) return "Good Afternoon";
    else return "Good Evening";
  };

  return (
    <div className="min-h-screen bg-gray-100 p-6">
      <div className="max-w-5xl mx-auto bg-white rounded-xl shadow-lg overflow-hidden">

        {/* Top Greeting Bar */}
        <div className="flex items-center justify-between px-6 py-4 border-b bg-blue-50">
          <div>
            <h2 className="text-lg text-gray-600">{user && getGreeting()},</h2>
            <h1 className="text-2xl font-bold text-blue-800">
              {user?.name || "Student"} ðŸ‘‹ Welcome back to your dashboard
            </h1>
          </div>
          <div>
            <img
              src={user?.profileImage || "/default-profile.png"} // fallback image
              alt="Profile"
              className="w-14 h-14 rounded-full border-2 border-blue-500 object-cover"
            />
          </div>
        </div>

        {/* Main Layout */}
        <div className="flex">
          {/* Sidebar */}
          <div className="w-1/4 border-r border-gray-200 bg-blue-100 p-4">
            <div className="space-y-2">
              <button
                onClick={() => setActiveTab("personal")}
                className={`w-full text-left px-4 py-2 rounded-lg font-medium ${
                  activeTab === "personal"
                    ? "bg-blue-600 text-white"
                    : "text-blue-800 hover:bg-blue-200"
                }`}
              >
                Personal Details
              </button>
              <button
                onClick={() => setActiveTab("professional")}
                className={`w-full text-left px-4 py-2 rounded-lg font-medium ${
                  activeTab === "professional"
                    ? "bg-blue-600 text-white"
                    : "text-blue-800 hover:bg-blue-200"
                }`}
              >
                Professional Details
              </button>
              <button
                onClick={() => setActiveTab("education")}
                className={`w-full text-left px-4 py-2 rounded-lg font-medium ${
                  activeTab === "education"
                    ? "bg-blue-600 text-white"
                    : "text-blue-800 hover:bg-blue-200"
                }`}
              >
                Education Details
              </button>
            </div>
          </div>

          {/* Content Section */}
          <div className="w-3/4 p-6">
            {!user ? (
              <p className="text-gray-500">Loading...</p>
            ) : (
              <>
                {activeTab === "personal" && <PersonalDetails user={user} />}
                {activeTab === "professional" && <ProfessionalDetails user={user} />}
                {activeTab === "education" && <EducationDetails user={user} />}
              </>
            )}
          </div>
        </div>
      </div>
    </div>
  );
}
